:root {
  --transition-speed: 600ms;
  --text-primary: #e5b8f4;
  --text-secondary: #e5b8f4;
  --bg-primary: #c147e9;
  --bg-secondary: #c147e9;
  --purple1: #e5b8f4;
  --purple2: #c147e9;
  --purple3: #810ca8;
  --purple4: #2d033b;
}

.right {
  margin-left: 5rem;
}

.navbar {
  position: fixed;
  background-color: var(--purple4);
  transition: width 600ms ease;
  overflow: scroll;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

/* .nav-item:last-child {
  margin-top: auto;
} */
.nav-link-first {
  display: flex;
  align-items: center;
  height: 5rem;
  margin-top: 0.5rem;
  margin-left: 1rem;
  color: var(--text-primary);
  text-decoration: none;
  /* filter: grayscale(100%) opacity(0.7); */
  transition: var(--transition-speed);
}
.nav-link {
  display: flex;
  align-items: center;
  height: 3rem;

  margin-top: 0.5rem;
  margin-left: 1rem;
  margin-right: 1rem;
  color: var(--text-primary);
  text-decoration: none;
  /* filter: grayscale(100%) opacity(0.7); */
  transition: var(--transition-speed);
}

.nav-link-first:hover {
  /* filter: grayscale(0%) opacity(1); */
  background: var(--purple1);
  color: var(--purple3);
}
.nav-link:hover {
  /* filter: grayscale(0%) opacity(1); */
  background: var(--purple1);
  color: var(--purple3);
}

.link-text {
  display: none;
  margin-left: 0rem;
}
.nav-link-first svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: #ff7eee;
}

.fa-secondary {
  color: #df49a6;
}

.fa-primary,
.fa-secondary {
  transition: var(--transition-speed);
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: var(--purple1);
  background: var(--purple4);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: var(--transition-speed);
}

.logo-text {
  display: inline;
  position: absolute;
  left: -500px;
  transition: var(--transition-speed);
}

.navbar:hover .logo svg {
  transform: rotate(-180deg);
}

/* Small screens */
@media only screen and (max-width: 600px) {
  .navbar {
    bottom: 0;
    width: 100vw;
    height: 5rem;
    z-index: 1;
  }

  .logo {
    display: none;
  }

  .navbar-nav {
    flex-direction: row;
  }

  .nav-link {
    justify-content: center;
  }

  .col {
    display: flex;
    position: relative;
  }

  .right {
    margin: 0;
  }
}

/* Large screens */
@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 14rem;
    z-index: 1;
  }

  .navbar:hover .link-text {
    display: inline;
  }

  .navbar:hover .logo svg {
    margin-left: 11rem;
  }

  .navbar:hover .logo-text {
    left: 0px;
  }

  .col {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    position: relative;
  }
}

